.App {
  text-align: center;
  align-items: center;
  margin: 0 auto;
  margin-top: 10%;
}

.heading{
  color: white;
  margin-top: 10%;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.btn-style{
  border-radius: 15px;
  background-color: rgb(232, 219, 245);
  padding: 10px;
  color: #282c34;
  border: 1px solid transparent;
  outline: none;
}

.btn-style:hover{
  text-shadow: rgba(red, green, blue, alpha);
}
